import styled from "styled-components";

export default function Filter({items = [], active = '', onChange}) {
    return (
        <FilterContainer>
            <FilterContainerBox>
                    {items.map((item, index) => (
                        <FilterContainerBoxItem key={index} onClick={() => onChange && onChange(item.value)}
                             className={`${item.value === active ? `active` : ``}`}>
                            <span>#{item.title}</span>
                        </FilterContainerBoxItem>
                    ))}
            </FilterContainerBox>
        </FilterContainer>
    )
}

const FilterContainer = styled.div`
    display: flex;
    width: 100%;
    box-sizing: border-box;
    justify-content: center;
    flex-direction: column;
    position: relative;
    margin: 30px 0;
`;
const FilterContainerBox = styled.nav`
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: center;
    list-style: none;
    gap: 10px;
    position: relative;
    margin: 0;
    padding: 0;
    @media screen and (max-width: 600px) {
        flex-direction: row;
        flex-wrap: wrap;
    }
`
const FilterContainerBoxItem = styled.li`
    cursor: pointer; 
    max-width: calc(100% / 4 - 48px);
    width: auto;
    padding: 0 24px;
    display: flex;
    min-height: 48px;
    align-items: center;
    justify-content: center;
    text-align: center;
    transition: all 0.3s ease-in-out;
    box-sizing: border-box;
    border-radius: 24px;
    & > span {
        font-style: normal;
        font-weight: bold;
        font-size: 1.2rem;
        color: #219653;
    }

    &.active {
        background: #219653;
        color: #FFFFFF;
        font-weight: bold;
        & > span {
            color: #FFFFFF;
        }
    }

    @media screen and (max-width: 1000px) {
        max-width: calc(100% / 3 - 48px);
    }
    @media screen and (max-width: 800px) {
        max-width: calc(100% / 2 - 48px);
    }
    @media screen and (max-width: 600px) {
        max-width: calc(100% - 48px);
        flex-wrap: wrap;
    }
`